// @ts-nocheck
import React, { useContext, useEffect, useState } from 'react'
import style from './index.module.scss'
import Input from '../../../../Input'
import { retainNumber } from '../../../../../../utils/utils'
import { fabric } from 'fabric';
import { Context } from '../../../../../Draw'
import useLock, { isControlsInRatioVisible } from '../../../../../Draw/hooks/useLock'
import { floatRound } from '../../../../../../utils/calculate';


interface SizeProps {
  getActiveObject?: () => fabric.Object
  isWorkSpace?: boolean
  showRation?: boolean
}

const Size: React.FC<SizeProps> = ({getActiveObject, showRation, isWorkSpace}) => {
  const {canvas, workSpace} = useContext(Context)
  const {changeInRatioLock} = useLock()
  const [width, setWidth] = useState('')
  const [height, setHeight] = useState('')
  const [lockRatio, setLockRatio] = useState(false)

  useEffect(() => {
    getAttr()
  }, [canvas, getActiveObject])

  useEffect(() => {
    if (!canvas) return
    canvas.on('object:modified', getAttr)
    return () => {
      canvas.off('object:modified', getAttr)
    }
  }, [canvas])

  const getAttr = () => {
    const activeObject = getActiveObject()
    if (!activeObject) return
    setWidth(floatRound(activeObject.width))
    setHeight(floatRound(activeObject.height))
    setLockRatio(isControlsInRatioVisible(activeObject))
  }
  /**
   * 
   * @param value
   */
  const onWidthChange = (value) => {
    const activeObject = getActiveObject()
    if (!activeObject) return
    value = retainNumber(value)
    if (isWorkSpace) {
      workSpace?.setSize(value, height)
      setWidth(value)
      return;
    }
    activeObject.set({width: +value})
    canvas?.renderAll()
    setWidth(value)
  }
  /**
   * 
   * @param value
   */
  const onHeightChange = (value) => {
    const activeObject = getActiveObject()
    if (!activeObject) return
    value = retainNumber(value)
    if (isWorkSpace) {
      workSpace?.setSize(width, value)
      setHeight(value)
      return;
    }
    activeObject.set({height: +value})
    canvas?.renderAll()
    setHeight(value)
  }

  const changeLock = () => {
    changeInRatioLock(lockRatio)
    setLockRatio(prevState => !prevState)
  }
  return (
    <div className={style.size}>
      <Input
        afterText=''
        title=''
        value={width}
        onChange={e => onWidthChange(e.target.value)}
      />
      <Input
        afterText=''
        title=''
        value={height}
        onChange={e => onHeightChange(e.target.value)}
      />
      {
        showRation ? <div className={style.ratio}>
          <div className={style.title}></div>
          <div className={style.content} onClick={changeLock}>
            <img
              src={lockRatio ?
                "https://ossprod.jrdaimao.com/file/1690955813239228.svg" :
                "https://ossprod.jrdaimao.com/file/1690425288688481.svg"}
              alt=""
            />
          </div>
        </div> : <div className={style.ratio}></div>
      }
    </div>
  )
}

export default Size
